<template>

 


  <div class="container"> 
  <header>
    <h3>Yemekçiler</h3>
  </header>

    <div class="table-wrapper">
      <table class="table-users">
        <tr>
          <th style="width: 50%;">Yemekçi İsmi</th>
          <th style="width: 50%;">Grup</th>
        </tr>
                            <tr v-for="yemekci in YemekcilerDizi" :key="yemekci.key">
                        <td>{{ yemekci.yemekci_isim }}</td>
                        <td>{{ yemekci.yemekci_grup }}</td>
                    
                    </tr>
      </table>
       

    </div>

   

  </div>

</template>

<script>
import { db } from "../firebaseDb";

export default {
  data() {
    return {
      YemekcilerDizi: [],
    };
  },
  created() {
   
db.collection("yemekciler").orderBy("yemekci_grup", "asc").onSnapshot((snapshotChange) => {
      this.YemekcilerDizi = [];
      snapshotChange.forEach((doc) => {
        this.YemekcilerDizi.push({
          key: doc.id,
          yemekci_isim: doc.data().yemekci_isim,
          yemekci_grup: doc.data().yemekci_grup,
        });
      });
    });
  },
};
</script>

